import React, { Component } from 'react';

import './login.scss';

import { withRouter } from 'react-router-dom';

import axios from 'axios';

class login extends Component {
	constructor(props) {
		super(props);
		this.state = {
			username: '',
			password: '',
			isLoading: false,
			invalid: false,
			active: true,
			type: 0
		};
		this.changeLogin = props.changeLogin;
	}

	setEamilValue = (event) => {
		let username = event.target.value;
		this.setState({ username: username });
	};

	setPasswordValue = (event) => {
		let password = event.target.value;
		this.setState({ password: password });
	};

	sendLoginRequest = (event) => {
		event.preventDefault();
		this.setState({ isLoading: true });
		const formData = new FormData();
		formData.append('username', this.state.username);
		formData.append('password', this.state.password);
		console.log(formData);
		axios
			.post('http://stage.medicento.com:8080/api/app/admin_login/', formData)
			.then((response) => {
				console.log(response);
				if (response.data.message === 'User Logged In') {
					this.changeLogin(true);
					this.setState({ isLoading: false, invalid: false });
					this.props.history.push('/admin');
				} else {
					this.setState({ invalid: true, isLoading: false });
				}
			})
			.catch((err) => {
				this.setState({ isLoading: false });
			});
	};

	render() {
		const active = this.state.active;
		const type = this.state.type;

		return (
			<div className="login">
				<div className="container">
					<form className="login_form" onSubmit={this.sendLoginRequest}>
						<div className="row">
							<div className={`col-md-6 login_heading left_border ${active ? "active_login" : "non_active_login" }`}>
								Register
							</div>
							<div className={`col-md-6 login_heading right_border ${active ? "non_active_login" : "active_login" }`}>
								Sign In
							</div>
						</div>
						<div className="outer_box">
							<div className="one_stop_box">
								<div className="one_stop">One Stop Channel for all of your Medicinal Needs</div>
							</div>
							<div className="row choose_type">
								<div className="col-md-1 "/>
								<div className={`col-md-3 right_margin ${type == 0 ? "choose_type_active_login" : "choose_type_non_active_login" }`}>
									Retailer
								</div>
								<div className={`col-md-3 ${type == 1 ? "choose_type_active_login" : "choose_type_non_active_login" }`}>
									Distributor
								</div>
								<div className={`col-md-3 ${type == 2 ? "choose_type_active_login" : "choose_type_non_active_login" }`}>
									Manufacturer
								</div>
							</div>
							<div className="input_box">
								<input placeholder="Username or Email" />
							</div>
							<div className="input_box">
								<input placeholder="Password"/>
							</div>
							<div className="remember">
								<input type="checkbox"/> Remember Me
								<a href="/forget" className="left_float forget">Forgot Password?</a>
							</div>
							<div className="new_user">
								New User? <a href="/sign_up"> Sign Up</a>
							</div>
						</div>
					</form>
				</div>
			</div>
		);
	}
}

export default withRouter(login);
